import React, { useEffect, useState } from "react";
import PropTypes from 'prop-types';
import AssesmentRadio from "./AssesmentRadio";

function AssesmentCheckBox({
    label,
    idx,
    refCheckBoxJawaban,
    refSetCheckBoxJawaban,
    idxSoal,
    opsi,
    soal
}) {

    const [flagReRender, setFlagReRender] = useState(false);
    const [showChild, setShowChild] = useState(false);
    const [soalChild, setSoalChild] = useState({});
    const [refChildJawaban, setRefChildJawaban] = useState([]);

    const [isChecked, setIsChecked] = useState(refCheckBoxJawaban[idxSoal].jawaban[idx] != '');

    useEffect(() => {
        if (refCheckBoxJawaban[idxSoal].nested_jawaban) {
            let result = Object.keys(refCheckBoxJawaban[idxSoal]?.nested_jawaban).map((key) => refCheckBoxJawaban[idxSoal].nested_jawaban[key]);
            if (result.jawaban != '') {
                setRefChildJawaban(result);
                soal.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.forEach(element => {
                    if (result[idx].id == element?.assesment_assesment_mapping_assesment_idToassesment?.id) {
                        const newEl = {
                            ...element.assesment_assesment_mapping_assesment_idToassesment,
                            parent_id: element.parent_assesment_id,
                            opsi_jawaban: element.opsi,
                            opsi_jawaban_index: idx,
                        };
                        setSoalChild(newEl);
                        setShowChild(true);
                    }
                });
            }
        }
    }, []);

    useEffect(() => {
        if (refCheckBoxJawaban[idxSoal].nested_jawaban) {
            let result = Object.keys(refCheckBoxJawaban[idxSoal]?.nested_jawaban).map((key) => refCheckBoxJawaban[idxSoal].nested_jawaban[key]);
            if (result.jawaban != '') {
                setRefChildJawaban(result);
                soal.assesment_mapping_assesment_mapping_parent_assesment_idToassesment.forEach(element => {
                    if (result[idx].id == element?.assesment_assesment_mapping_assesment_idToassesment?.id) {
                        const newEl = {
                            ...element.assesment_assesment_mapping_assesment_idToassesment,
                            parent_id: element.parent_assesment_id,
                            opsi_jawaban: element.opsi,
                            opsi_jawaban_index: idx,
                        };
                        setSoalChild(newEl);
                        setShowChild(true);
                    }
                });
            }
        }
    }, [isChecked]);

    const handleChangeCheckbox = (checked) => {

        if (checked) {
            let temp_check_box = refCheckBoxJawaban;
            temp_check_box[idxSoal].jawaban[idx] = opsi;
            refSetCheckBoxJawaban(temp_check_box);
        } else {
            let temp_check_box = refCheckBoxJawaban;
            temp_check_box[idxSoal].jawaban[idx] = '';
            if (temp_check_box[idxSoal].nested_jawaban) {
                temp_check_box[idxSoal].nested_jawaban[soalChild.id].jawaban = '';
            }
            refSetCheckBoxJawaban(temp_check_box);
        }
        setIsChecked(isChecked => !isChecked);
    }

    const handleChange = event => {
        handleChangeCheckbox(event.target.checked);
    };

    const handleAssesmentChangeRadio = (event, indexChildSoal) => {
        console.log('FIRE')
        let temp_check_box = refCheckBoxJawaban;
        temp_check_box[soalChild.parent_id].jawaban[idx] = opsi;
        temp_check_box[idxSoal].nested_jawaban[indexChildSoal].jawaban = event.target.value;
        refSetCheckBoxJawaban(temp_check_box);
        setIsChecked(true);
    }

    return (
        <div className={"flex items-center"}>
            <div className="flex flex-col">
                <div>
                    <input
                        checked={isChecked}
                        onChange={handleChange}
                        id={"checked-checkbox-" + idx}
                        type="checkbox"
                        value={isChecked}
                        className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                    <label htmlFor={"checked-checkbox-" + idx} className={"ml-2 text-md font-large text-gray-900"}>{label}</label>
                </div>
                {showChild && (
                    <div className="pl-10 pt-1">
                        {soalChild?.assesment_opsi?.map((el, i) => {
                            return (
                                <div className={"flex items-center"} key={el.key_opsi}>
                                    <div className="flex flex-col">
                                        <div>
                                            <input
                                                checked={refCheckBoxJawaban[idxSoal].nested_jawaban[soalChild.id].jawaban === el.key_opsi}
                                                onChange={(e) => {
                                                    handleAssesmentChangeRadio(e, soalChild.id);
                                                    setFlagReRender(flagReRender => !flagReRender);
                                                }}
                                                id={"checked-checkbox-" + `${el.key_opsi}-${soalChild.id}`}
                                                name={"opsi_nested_" + soalChild.id}
                                                type="radio"
                                                value={el.key_opsi}
                                                className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                                            <label htmlFor={"checked-checkbox-" + `${el.key_opsi}-${soalChild.id}`} className={"ml-2 text-md font-large text-gray-900 cursor-pointer"}>{el.label_opsi}</label>
                                        </div>
                                    </div>
                                </div>)
                        })}
                    </div>
                )}
            </div>
        </div>);
}

AssesmentCheckBox.propTypes = {
    label: PropTypes.string.isRequired,
    idx: PropTypes.number.isRequired,
    refCheckBoxJawaban: PropTypes.object.isRequired,
    refSetCheckBoxJawaban: PropTypes.func.isRequired,
    idxSoal: PropTypes.number.isRequired,
    opsi: PropTypes.string.isRequired,
    soal: PropTypes.object.isRequired,
}

export default AssesmentCheckBox;
